        const A1BuffCtrl = charBCs.find(({ index }) => index === 1);
        const hydroInfusedES = A1BuffCtrl?.inputs?.includes("Hydro");
        const SDPdifference = (hydroInfusedES ? 120 : 100) - (inputs?.[0] || 0);
