const windGiftBnValue = ({ toSelf, inputs, char, partyData }) => {
  const level = toSelf
    ? getFinalTlLv(char, Faruzan.actvTalents[2], partyData)
    : inputs?.[0] || 0;
  return level ? round1(23.7 * tlLvMults[2][level]) : 0;
};

